@use "sass:math";
@use "sass:meta";
@use 'variables' as *;

/*****************************************
 * n * n * https: //css-tricks.com/snippets/sass/
******************************************/
@function strip-unit($number) {
  @if meta.type-of($number) == "number" and not math.is-unitless($number) {
    @return math.div($number, $number * 0 + 1);
  }

  @return $number;
}
/*****************************************
 * pxemn * n * https: //webdou.net/sass-rem/
 * Sassmath.div(/)
******************************************/
@function prem($pixels) {
  @return math.div($pixels , strip-unit($base-font-size)) * 1rem;
}

/*****************************************
 * vwn * n * https://webdou.net/sass-vw/
 * Sassmath.div(/)
******************************************/
@function vw($screen-size, $pixels) {
  @return math.div($pixels , $screen-size) * 100vw;
}

