@use "sass:math";

// 
// SPPC
$break-flg: 0 !default; // 0:SP1:PC

$breakpoints: null;
@if $break-flg == 0 {
  $breakpoints: (
    "sm": "screen and (min-width: 400px)",
    "md": "screen and (min-width: 768px)",
    "lg": "screen and (min-width: 1000px)",
    "xl": "screen and (min-width: 1400px)"
  );
} @else {
  $breakpoints: (
    "sm": "screen and (max-width: 399px)",
    "md": "screen and (max-width: 767px)",
    "lg": "screen and (max-width: 999px)",
    "xl": "screen and (max-width: 1399px)"
  );
}

// inner
$inner: 1870px;

// 
$padding-pc: 25px; // PC
$padding-sp: 25px; // 

// 
$font-family-base: 'Noto Sans JP', sans-serif; // 
$regular: 400;
$medium: 500;
$semi-bold: 600;
$bold: 700;
$extra-bold: 800;
$font-family-mincho: 'Sawarabi Mincho', sans-serif;
$font-family-heisei: 'DFPHSMincho', sans-serif;
// 
$base-font-size: 15px;

// Color
$color-black:#000;
$color-theme: #957B80;
$color-theme-right: #D1C2C5;
$color-white: #fff;
$main-gradient:linear-gradient(180deg, #957B80 52.88%, #B59F94 100%);



